// 注册全局组件
import Vue from 'vue'
import pullUpUi from 'COMPONENTS/layout/pullUpUi'
import { Toast } from 'mint-ui'

Vue.toast = ({title, position = 'center', duration = 2000, callBack}) => {
	Toast({
	  message: title,
	  position: position,
	  duration: duration
	})
	if (callBack) {
		setTimeout(() => {
			callBack()
		}, duration)
	}
}


Vue.component('pullUpUi', pullUpUi)
