import {
  SHOW_MSG
} from '../mutation-types'

import {
  uploadApi
} from 'API/common'

const state = {
  groupLists: []
}

const mutations = {}

const getters = {
  message: state => state.message
}

const actions = {
  showDialog(store) {
    store.commit(SHOW_MSG)
  },
  postUploadConfigApi(store, params) {
    return postUploadConfigApi(params).then(res => {
      store.commit(SHOW_MSG, res.data.data)
      return res
    })
    .catch(error => Promise.reject(error.data || {}))
  }
}

export default {
  state,
  mutations,
  getters,
  actions
}
