import axios from 'axios'
import { Loading } from 'element-ui'
import { removeAccessToken, getAccessToken } from '@/store/cacheService'
import localstorage from 'UTILS/localstorage'
let login = false
let loadingInstance = null
let requestNum = 0
// let urlparams = new URLSearchParams(location.search)

export const API_ROOT = process.env.VUE_APP_API
// console.log(urlparams)
axios.defaults.baseURL = API_ROOT
export const request = ({ url, method, params = {} }) => {
  let access_id = localstorage.get('access_id')

  axios.defaults.headers.common['Wechat-Version'] = 101

  if (access_id) {
    axios.defaults.headers.common['Authorization-Official'] = access_id
  }

  let sessionToken = localstorage.get('sessionToken')
  let token = localstorage.get('token')
  let addHttpHead = axios.defaults.headers.common

  // token = '14300910b255e8ca508598bc0597c695'
  if (sessionToken && !token) {
    addHttpHead['Authorization-Wechat'] = sessionToken
  } else {
    delete addHttpHead['Authorization']
  }
  if (token) {
    addHttpHead['Authorization'] = token
  } else {
    delete addHttpHead['Authorization']
  }

  if (!params.noLoading) {
    if (requestNum === 0) loadingInstance = Loading.service({background: 'transparent'})
    requestNum++
    delete params.noLoading
  }
  return axios[method](url, method === 'get' ? { params } : params).then(res => {
    requestNum--
    if (requestNum <= 0) loadingInstance.close()

    if (res.data.code === 400) return Promise.reject(res.data)

    return Promise.resolve(res.data)
  }).catch(err => {
    requestNum--
    if (requestNum <= 0) loadingInstance.close()
    switch(err.response.status) {
      case 401:
        localstorage.remove('access_id')
        document.cookie = `oauth_token=''`
        let path = location.href
        location.href = `${API_ROOT}/wechat/oauth/official?redirect_url=${encodeURIComponent(path)}`
        break
    }
    return Promise.reject(err.response.data)
  })
}
