<template>
  <div id="app">
    <router-view />
  </div>
</template>
<script>

import Vue from 'vue'
import Component from 'vue-class-component'
import localstorage from 'UTILS/localstorage'
import wechatMixin from '@/mixins/wechat'

@Component({
  name: 'app',
  watch: {
  	'$route': {
      handler (route) {
      	let access_id = localstorage.get('access_id')
        if (route.query.access_id && !access_id) {
        	localstorage.set('access_id', route.query.access_id)
          let path = location.href.replace('access_id', 'formAccessId')
          location.href = path
        }
      },
      immediate: true
    }
  },
  mixins: [wechatMixin]
})

export default class App extends Vue {
  mounted() {
  }
}
</script>
<style lang="scss">
@import 'assets/styles/index.scss';
.mint-toast {
  width: 60%;
}
</style>