import Cache from 'web-storage-cache';

const lsCache = new Cache;

const local = {
  token: '',
  userInfo: {}
}

class CommonStorage {
  
  constructor(key, exp = null) {
    this.key = key
    this.exp = exp
    this.storage = lsCache
  }
  
  save(value) {
    const options = this.exp ? { exp: this.exp } : null;
    this.storage.set(this.key, value, options);
  }

  load() {
    return this.storage.get(this.key);
  }
}

export function saveAccessToken(token, expiredIn) {
  lsCache.set(local.token, token, { exp: expiredIn })
}

export function getAccessToken() {
  return lsCache.get(local.token)
}

export function removeAccessToken() {
  lsCache.delete(local.token)
  ssCache.delete(local.userInfo)
}

export function saveUserInfo(userInfos, expiredIn) {
  lsCache.set(local.userInfo, userInfos, { exp: expiredIn })
}

export function getUserInfo(userInfos, expiredIn) {
  return lsCache.get(local.userInfo)
}

export const cachedUserInfo = new CommonStorage(local.userInfo, 60 * 60 * 24 * 7 * 1000)
